class ParType {
    static TEXT= 1;
    static DATA_PARENT = 2;
    static DATA_CHILD = 3;
    static GROUPS_CHOOSER = 4;
    static FEE_SCHEDULE = 5;
    static INDIVIDUAL_FEE_CHECKBOX = 6;
    static CUSTOM_CHECKBOX = 7;
    static SMS_CODE = 8;
    static EMAIL_CONTENT = 9;
    static SMS_CONTENT = 10;
    static PARTICIPANTS = 12;
}

function MikrotabelkaDaneUczestnika({dane,title}) {
    const listChetni = Object.entries(dane).map(([key, value], index) => {
        if(key === "id") {
            return;
        }

        return (
            <tr key={index}>
                <td>{key}</td>
                <td>{value}</td>
            </tr>
        );
    });
        
    return (
        <table>
            <tbody>
                <tr>
                    <th colSpan="2">{title}</th>
                </tr>
                {listChetni}
            </tbody>
        </table>
    );
}

function MikrotabelkaWypParagraf({dane}) {
    const type = dane["type"];
    const data = dane["data"];
    const par = dane["paragraf"];

    function ChoosedGroups({data}) {
        if(data["groups"] == null || data["groups"].length === 0) {
            return null;
        }
        const grupki = data["groupsEntities"].map((grupka, i) =>
            <tr key={i}>
                <td>{grupka["nazwa"]}</td>
            </tr>
        );
        return (
            <table>
                <tbody>
                <tr key="0">
                    <th>Propozycje grup</th>
                </tr>
                {grupki}
                </tbody>
            </table>
        );
    }
    function CustomCheckbox({data,par}) {
        const checked = data["checked"];
        const parData = par["data"];
        const required = parData["required"];
        const name = par["name"];
        return (
            <table>
                <tbody>
                <tr><th>Checkbox</th></tr>
                <tr><th>{name}</th></tr>
                <tr><th>{required ? "wymagany" : "niewymagany"}</th></tr>
                <tr><td>{checked ? "zaznaczony!" : "niezaznaczony!"}</td></tr>
                </tbody>
            </table>
        );
    }

    if(type === ParType.GROUPS_CHOOSER) {
        return <ChoosedGroups data={data}/>
    } else if(type === ParType.CUSTOM_CHECKBOX) {
        return <CustomCheckbox par={par} data={data}/>
    }
}

function ChetnySzczegoly({uczestnik}) {
    let [umowa, setUmowa] = React.useState(null);
    let [error, setError] = React.useState(null);
    const fetchUmowa = ()=>{
        fet("uczestnicy/"+uczestnik["id"]+"/umowy").then(umowy_arr=> {
            //const umowy_arr = hat_embedded(packet);
            /*fet(hat_link(umowy_arr[0],"wypelnioneParagrafy")).then(packet=> {
                setWypPar(packet);
            }).catch(err=>setError(err));*/
            if(umowy_arr == null || umowy_arr.length === 0) {
                return;
            }

            setUmowa(umowy_arr[0]);
        }).catch(err=>setError(err));
    }
    if(error != null) {
        return (
            <span>error!</span>
        );
    }
    if(umowa == null) {
        fetchUmowa();
        return (
            <span>wczytuję...</span>
        );
    }

    const openSave = (miejsce,uczestnik) =>{
        let box = Dialog.build().inner((
            <PrzydzielUczestnika miejsce={miejsce} uczestnik={uczestnik}/>
        )).centered(true).title("zapisywanie uczestnika").draw();
    }

    function InneDaneKandydata({wypPar}) {
        if(wypPar == null) {
            return null;
        }
        const map = wypPar.map((par,i)=><MikrotabelkaWypParagraf key={i} dane={par}/>)
        return map;
    }
    const wyppar = umowa["wypelnioneParagrafy"];
    const formularz = umowa["formularz"];

    return (
        <div className="box_chetni">
            <div className="dane">
                <MikrotabelkaDaneUczestnika title="Dane rodzica" dane={uczestnik["daneRodzica"]}/>
                <MikrotabelkaDaneUczestnika title="Dane dziecka" dane={uczestnik["daneDziecka"]}/>
                <InneDaneKandydata wypPar={wyppar}/>
            </div>
            <div className="przyciski">
                <input onClick={()=>openSave(formularz["miejsce"],uczestnik)} type="button" value="zapisz na zajęcia"/>
                <input type="button" value="ukryj zgłoszenie"/>
                <input type="button" value="przejdź do konwersacji SMS"/>
            </div>
        </div>
    );
}

export default function ChetniDialog({miejsceId}) {
    //const chetni = await fet("miejsca/"+miejsceId+"/chetni_uczestnicy");
    let [chetni, setChetni] = React.useState(null);
    let [error, setError] = React.useState(null);
    const fetchChetni = ()=>{
        fet("miejsca/"+miejsceId+"/chetni_uczestnicy").then(res=>setChetni(res)).catch(err=>setError(err));
    }
    const openDetails = (uczestnik) =>{
        let box = Dialog.build().inner((
            <ChetnySzczegoly uczestnik={uczestnik}/>
        )).centered(true).title("szczegóły uczestnika").draw();
    }
    
    if(error != null) {
        return (
            <div>
                <span>Wystąpił błąd!</span>
                <span>{error}</span>
            </div>
        );
    }
    if(chetni == null) {
        fetchChetni();

        return (
            <div>
                <span>Wczytywanie...</span>
            </div>
        );
    }
    if(chetni.length === 0) {
        fetchChetni();

        return (
            <div>
                <span>Brak chętnych!</span>
            </div>
        );
    }
    
    const listChetni = chetni.map( (chetny,i) =>{
        const daneRodzica = chetny["daneRodzica"];
        const daneDziecka = chetny["daneDziecka"];
        return(
            <tr key={i}>
                <td>{daneRodzica.imie}</td>
                <td>{daneRodzica.nazwisko}</td>
                <td>{daneRodzica.telefon}</td>
                <td>{daneRodzica.email}</td>
                <td>{daneDziecka.imie}</td>
                <td>{daneDziecka.nazwisko}</td>
                <td><input onClick={() => openDetails(chetny)} type="button" value="wybierz"/></td>
            </tr>
        );
    });
    return (
        <div className="chetni_scroller">
            <table>
            <tbody>
                <tr>
                    <th colSpan="4">Opiekun</th>
                    <th colSpan="2">Dziecko</th>
                    <th colSpan="2">Ogólne</th>
                </tr>
                <tr>
                    <th>Imie</th>
                    <th>Nazwisko</th>
                    <th>Telefon</th>
                    <th>Email</th>
                    <th>Imie</th>
                    <th>Nazwisko</th>
                    <th>Akcja</th>
                </tr>
                {listChetni}
            </tbody>
       </table>
        </div>
    );
}

function SelectGrupaTaneczna({miejsce,def}) {
    let [grupy, setGrupy] = React.useState(null);
    let [error, setError] = React.useState(null);
    const fetchGrupy = ()=>{
        fet(`miejsca/${miejsce}/grupy`).then(packet=> {
            setGrupy(hat_embedded(packet));
        }).catch(err=>setError(err));
    }
    if(grupy == null) {
        fetchGrupy();

        return(
            <span>wczytuje...</span>
        );
    }

    const gr = grupy.map((grupa,i)=>
        <option key={i} value={grupa["id"]}>{grupa["nazwa"]}</option>
    )
    return (
        <select>
            {gr}
        </select>
    );
}

function PrzydzielUczestnika({miejsce,uczestnik}) {
    return(
        <div>
            <table>
                <tbody>
                    <tr>
                        <td>grupa taneczna</td>
                        <td><SelectGrupaTaneczna miejsce={miejsce}/></td>
                    </tr>
                </tbody>
            </table>
        </div>
    );
}